#Card {
    background-color: aquamarine;
}
#h1{
    animation: blink 2s infinite;
}
@keyframes blink{
    0%{
        color: #565656;
        
    }
    25%{
        color: blue;
    }
    50%{
        color: coral;
    }
    75%{
        color:darkcyan;
    }
    100%{
        color: darkolivegreen;
    }
}