#form{
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 3%;
    border-bottom: 5px double #5a0606;
    border-radius: 25%;
    border-top: 5px double #5a0606;
    padding: 10px;
    margin-bottom: 25px;

}

#feedback:hover,#Email:hover,#name:hover {
    background-color: greenyellow;
}
#submit{
    display: block;
}
#submit:hover {
    border: 5px double chocolate;
    border-radius: 50px;
    background-color: green;
}
#form{
    animation: gradient 1s  infinite;
}
@keyframes gradient{
    0%{
        background: linear-gradient(to right,red,orange);
    }
    25%{
        background: linear-gradient(to bottom,orange,red);
    }
    50%{
        background: linear-gradient(to left,red,orange);
    }
    75%{
        background: linear-gradient(to top,orange,red);
    }
    100%{
        background: linear-gradient(red,orange);
    }
}